import React from "react";
import {
  Container,
  makeStyles,
  Typography,
  useMediaQuery,
  useTheme,
} from "@material-ui/core";
import img from "../../assets/images/state/howToStart.webp";
import { Box } from "@mui/system";

const HowToStart = ({ t }) => {
  const classes = useStyles();
  const theme = useTheme();
  const tablet = useMediaQuery(theme.breakpoints.down(992));

  return (
    <section className={classes.HowToStart}>
      <Container
        maxWidth="lg"
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <Typography variant="h2" className={classes.title}>
          {t("howToStart")}
        </Typography>
        <Box
          component="img"
          src={img}
          style={tablet ? { width: "100%" } : { width: "66%" }}
          alt=""
        />
      </Container>
    </section>
  );
};

export default HowToStart;

const useStyles = makeStyles((theme) => ({
  HowToStart: {
    padding: "80px 0 114px",
  },

  title: {
    color: "#000",
    marginBottom: 32,

    [theme.breakpoints.down(992)]: {
      marginBottom: 24,
    },

    [theme.breakpoints.down(768)]: {
      marginBottom: 9,
    },
  },
}));
